        </div>
        <div v-else>
          <p>
            <b>"GOLD TICKET" MChJ</b> loyihamizni tanlaganingiz uchun o'z minnatdorchiligimizni bildiramiz!  Aynan siz uchun biz xaridlaringiz yorqin va unutilmas bo'ladigan platformani ishlab chiqdik.
