<script>
import axios from "axios";

export default {
  name: "Policy",
  data: () => ({
    user_id: 0,
    user: null
  }),
  async mounted() {
    const telegram = window.Telegram.WebApp;
    const telegramData = telegram.initDataUnsafe;
    const backButton = telegram.BackButton;
    //
    //
    this.user_id = telegramData.user.id
    // this.user_id = 5296876609
    // let user = null

    await axios
        .get('https://server.gold-ticket.uz/api/user/' + this.user_id)
        .then(response => {
          this.user = response.data.data
        })
        .catch(error => console.log(error));

    console.log(this.user)
    backButton.show()
    backButton.onClick(() => {
      this.$router.push('/')
    });

    function agreed (user_id) {
      axios
          .post('https://server.gold-ticket.uz/api/user/' + user_id, {
            agreed: 1,
          })
          .then(response => {
            this.$router.push('/')
          })
          .catch(error => {
            telegram.showPopup(error)
          })
    }
  }
}
</script>

